#floatMenu{left:50px;top:-70px;position:relative}._dropdown-menu{padding:1px 0;color:#76a6e6;position:absolute;margin:-2px 0 0 0;border-radius:2px;box-shadow:0 2px 4px 0 rgba(163,163,163,.5);border:solid 1px green;z-index:999;background-color:#f7f7f7;line-height:26px;min-width:100px;text-align:left;left:70px;margin-top:-60px}._dropdown-menu li{list-style-type:none;min-width:60px;height:35px;padding:5px 0 1 10px;color:green}._dropdown-menu li:hover{cursor:pointer;background-color:#cde2f6}.fa-chart:before{content:""}.fa-news:before{content:""}#showLogo,#showMore{display:none}.webOnly{display:none}ul{list-style-type:none;display:flex;justify-content:space-around;margin:0;min-width:70px;padding:0}li{display:inline-block;flex:0 1 auto;font-family:sans-serif;text-align:center}li a{display:block;text-align:center;padding:5px;text-decoration:none;width:50px;font-size:14px}li:hover{cursor:pointer}li:hover a{color:#d946ef}li:hover a>i{font-size:28px}li i{font-size:28px}.menuBar{width:150px;width:100%;box-shadow:0px 0px 3px rgba(50,50,50,.75);background-color:#f4f5f7;z-index:999}.itemActive{color:#d946ef}.itemActive>i{font-size:28px !important}.itemDisabled>i{font-size:28px !important}.itemDisabled{color:dimgray}@media only screen and (min-width: 635px){ul{flex-direction:column;min-width:unset;padding-left:5px}li{display:block;height:65px;padding:0 8px 0 4px}li a{padding:0px;width:unset}li hover a>i,li i{font-size:28px}.itemActive>i{font-size:28px !important}#showLogo,#showMore{display:block}.webOnly{display:block}#logo{height:38px;width:38px;padding-left:5px}li#showLogo a{text-align:left}.menuBar{position:unset;float:left;width:unset;padding:30px 0;height:100vh;bottom:unset;margin-right:30px}li:hover a>i{font-size:28px}}